body {
  background-color: white;
}

.flexinho {
  display: flex;
  justify-content: center;
  width: 100%;
}

.musicsCard {
  display: flex;
  justify-content: space-evenly;
  margin: 40px;
  width: 90%;
}

.musImg {
  margin-top: 10px;
  width: 200px;
}

.musicsInfo {
  height: 330px;
}

.musics {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.eachMusic {
  align-items: center;
  background-color: whitesmoke;
  border: 2px solid gray;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  margin-top: 10px;
  padding: 10px;
  text-align: center;
  width: 350px;
}

.track-title {
  display: flex;
  padding: 10px;
}

@media screen and (max-width: 768px) {
  .musicsCard {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    width: 95%;
  }

  .track-title {
    align-items: center;
  }

  .eachMusic {
    margin-bottom: 5px;
    margin-top: 5px;
    width: 300px;
  }

}
